import React from 'react';
import Progress from '@vkontakte/vkui/dist/components/Progress/Progress'
import PanelSpinner from '@vkontakte/vkui/dist/components/PanelSpinner/PanelSpinner'
import Counter from '@vkontakte/vkui/dist/components/Counter/Counter'
class StatisticBlock extends React.Component {


    constructor(props) {
        super(props);
        this.state= {
            minimumId: this.props.minimumId,
            statData: undefined,
            maxVotes: 0,
        };
    }
    async componentDidMount() {
        let json=await this.props.loader(this.state.minimumId);
        if (Array.isArray(json)){
            json.sort(function(a,b){return b.total-a.total;});
            this.setState({statData:json, maxVotes:json[0].total});
        } else {

        }
        //this.props.resize();
    }
    render() {
        return (

            <div id={this.props.mode+"_size"}>
                {this.state.statData==undefined && <PanelSpinner/>}
                {this.state.statData!=undefined && this.state.statData.map((item)=>(
                    <div className="subscribeRow" onClick={this.click} key={"sub_"+item.anime_id}>
                        <div className="subscribeRowFullContent">
                            <div className="subscribeRowContent">
                                <div
                                    className="subscribeRowTitleNameContainer">{item.anime_name}</div>
                                <div className="statisticRowProgressContainer">
                                    <Progress value={100*item.total/this.state.maxVotes}/>
                                </div>
                            </div>

                        </div>
                        <div>
                            <Counter type="primary">{item.sub}</Counter>
                            <Counter>{item.dub}</Counter>
                        </div>
                    </div>
                ))}
            </div>

        );

    }
}

export default StatisticBlock